import { Component, OnInit } from '@angular/core';
import { UploadFileeService } from '../upload-filee.service';
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  public text;
  constructor(private uploadservice: UploadFileeService) { }

  ngOnInit() {
    this.about();
  }
  about() {

    this.uploadservice.about().subscribe((data) => {
      console.log(this.text);
      this.text = data.about;
    }, (error) => {
      console.log(error);
    });
  }

}
