import { Component, OnInit } from '@angular/core';
import { UploadFileeService } from '../upload-filee.service';
@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.scss']
})
export class NewsComponent implements OnInit {
  public text;
  constructor(private uploadservice: UploadFileeService) { }

  ngOnInit() {
    this.news();
  }
  news() {

    this.uploadservice.about().subscribe((data) => {

      this.text = data.news;
    }, (error) => {
      console.log(error);
    });
  }
}
