import { Component, OnInit } from '@angular/core';
import { UploadFileeService } from '../upload-filee.service';
@Component({
  selector: 'app-mark',
  templateUrl: './mark.component.html',
  styleUrls: ['./mark.component.scss']
})
export class MarkComponent implements OnInit {

  public text2;
  constructor(private uploadservice: UploadFileeService) { }

  ngOnInit() {

    this.mark();
  }

  mark() {

    this.uploadservice.about().subscribe((data) => {
      console.log(data.markque);
      this.text2 = data.markque;
    }, (error) => {
      console.log(error);
    });
  }


}
